                                        timeouts: { value: localStorage.getItem('timeouts') ?? JSON.stringify({ home: { first: 0, second: 0, ot: 0, ot2: 0 }, away: { first: 0, second: 0, ot: 0, ot2: 0 } }) },
                                        fouls: { value: localStorage.getItem('fouls') ?? JSON.stringify({ home: { first: 0, second: 0, third: 0, fourth: 0 }, away: { first: 0, second: 0, third: 0, fourth: 0 } }) },
                                        teamNotes: { value: localStorage.getItem('teamNotes') ?? JSON.stringify({ home: '', away: '', p1: '', p2: '', p3: '', p4: '', p5: '', poss: '' }) },
                                        teamA: { value: localStorage.getItem('teamA') },
                                        teamB: { value: localStorage.getItem('teamB') },
