    const teamA = rostersH.find(it => it.component_fields.filter(cf => cf?.value === records[0]?.value).length > 0)?.component_fields.find(cf => cf.field_name === records[0]?.options?.filters[0]);
    const teamB = rostersA.find(it => it.component_fields.filter(cf => cf?.value === records[1]?.value).length > 0)?.component_fields.find(cf => cf.field_name === records[1]?.options?.filters[0]);
    const league = selectedComponent?.component_fields?.find(sf => sf.data_type_name === template.config.find(sc => sc.type === 'League')?.value && sf.field_name === template.config.find(sc => sc.type === 'League')?.label);
